@charset "UTF-8";
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: "Noto Serif", serif; }
  @media only screen and (max-width: 75em) {
    html {
      font-size: 50%; } }

.Main-photo {
  height: 90vh;
  width: 100%;
  object-fit: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-align: center; }
  .Main-photo__text {
    text-align: center;
    position: absolute;
    top: 13%;
    left: 50%;
    transform: translateX(-50%); }
    .Main-photo__text h1 {
      color: #4e4e4e;
      font-size: 1.8rem;
      margin-bottom: 3rem; }
    .Main-photo__text h2 {
      color: #fff;
      font-size: 0.8rem; }

.Main-text {
  height: min-content;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  font-weight: 300;
  margin: 2rem;
  padding: 2rem;
  border: 1px #4e4e4e solid;
  border-radius: 2px; }
  .Main-text__name {
    text-align: center;
    font-size: 1.8rem;
    margin-bottom: 0.8rem; }
  .Main-text__sub {
    text-align: center;
    letter-spacing: 3px;
    font-size: 1.5rem;
    margin-bottom: 2px; }

.Gallery-photo {
  width: 100%;
  align-items: center; }

.slick-prev:before,
.slick-next:before {
  color: black !important; }

.greeting {
  width: 100%;
  height: 100%;
  text-align: center;
  color: #4e4e4e;
  position: relative;
  display: flex; }
  .greeting__text {
    position: absolute;
    background: rgba(255, 255, 255, 0.75);
    margin: 2rem;
    padding: 3rem;
    position: relative; }
  .greeting__message-head {
    font-size: 2.5rem;
    margin-bottom: 3rem;
    font-weight: bold; }
  .greeting__message-body {
    font-size: 1.6rem;
    line-height: 3rem; }
  .greeting-span::before {
    content: "";
    width: 10rem;
    height: .15rem;
    background-color: #666;
    display: inline-block;
    margin: 2.5rem; }
  .greeting-parent {
    font-size: 1.5rem;
    font-weight: bold;
    line-height: 2.5rem;
    margin-bottom: 3.5rem; }
    .greeting-parent span {
      font-size: 1rem; }

.bg-video__content {
  opacity: 0.8;
  z-index: -2;
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover; }

.btn__contact {
  display: inline-block;
  width: 70vw;
  height: 3rem;
  font-size: 1rem;
  color: #4e4e4e;
  border-radius: 1.5rem;
  background-color: #fff;
  margin-bottom: 1.5rem;
  text-decoration: none;
  border: none;
  box-shadow: 0.5rem 0.5rem 2rem rgba(0, 0, 0, 0.2); }

.popup-content {
  width: 80vw;
  height: 40%;
  border-radius: 5px;
  background: #fff;
  color: #4e4e4e; }

.popup-overlay {
  background: rgba(0, 0, 0, 0.5); }

.popup-arrow {
  visibility: hidden !important; }

.gallery {
  display: inline-block;
  width: 100vw; }

.map-icon {
  /*width: 48px;*/
  width: 5%;
  height: auto;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 1rem;
  margin-bottom: 1rem; }

.map-center {
  align-items: center;
  text-align: center; }

.title {
  flex-direction: column;
  text-align: center;
  font-family: YesevaOne;
  font-size: 400%;
  margin-top: 5rem;
  margin-bottom: 2rem; }

.map-text {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-family: BMJUAOTF;
  font-size: 140%;
  margin-bottom: 2rem; }

.map-info {
  font-family: BMJUAOTF;
  font-size: 120%;
  margin-bottom: 2rem;
  display: block; }

